* {
  margin: 0;
  padding: 0;
}

:root {
  --grey: grey;
  --black: black;
  --brown: rgb(66, 36, 18);
  --gold: gold;
  --silver: rgb(163, 163, 163);
  --green: rgb(56, 151, 80);
  --red: rgb(180, 12, 12);
  height: 100vh;
  background-image: linear-gradient(var(--brown) 0%, black 100%);
  background-size: cover;
}

body {
  margin: 0;
  font-family: 'Yatra One',-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  position: relative;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-image: linear-gradient(var(--brown) 0%, black 100%);
    background-size: cover;

    /* font-family: , cursive; */
}

.displayed {
  display:flex;
  flex-direction: column;
}

.centered {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.hidden {
  display:none
}


code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.outer-border{
  border: 10px ridge var(--gold);
  border-radius: 7px;
  padding:0px;
  margin:10px;
}

#page-title{
  border: 10px ridge var(--gold);

  padding:20px;
  margin:0px;
}

header {
  display: flex;
  flex-direction: row;
  justify-content: center;
  border-bottom: 10px ridge var(--gold);
  padding: 0px;
  border-radius: 1px;
  width: 100vw;
  background-image: radial-gradient(var(--grey) 0%, var(--grey) 50%, var(--grey)60%, var(--silver) 100%);
  margin: 0px;
}

nav{
  display: flex;
  align-items: left;
  
}

h1 {
  font-size: 80px;
  background-image: radial-gradient(var(--grey) 0%, var(--grey) 50%, var(--grey)60%, var(--silver) 100%);
  text-align: center;
}

h2 {
  font-size: 50px;
  margin: 10px;
  background-color: transparent;
  text-align: center;
}

h3 {
  font-size: 30px;
  margin: 10px;
}

h4 {
  font-size: 25px;
}

main {
  width: 100vw;
  height: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}

.container {
  width: 40vw;
  height: fit-content;
  margin-top: 20px;
  display: flex;
  flex-direction: row;
  border: 10px ridge var(--gold);
  justify-content: space-around;
  background-color: var(--grey);
  border-radius: 5px;
  background-image: linear-gradient(var(--silver) 0%, var(--grey) 35%);
  box-shadow: 2px 2px 10px black;

}

.menu-btn-div {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

#char-select-header{
  font-size: 60px;
  text-decoration: underline;
  text-underline-offset: 10px;
  background-image: none;
}

.card {
  position: relative;
  width: 40%;
  height: fit-content;
  margin: 10px;
  display: flex;
  flex-direction: column;
  border: 10px ridge var(--gold);
  padding: 10px;
  background-color: var(--grey);
  border-radius: 5px;
  box-shadow: 2px 2px 10px black;
}


#char-name {
  font-size: 50px;
  position: relative;
  top: 0px;
}

p {
  font-size: 20px;
  margin: 5px;
  margin-left: 25px;
  width: fit-content;
  border-radius: 10px;
}

span {
  border-radius: 90px;
}

/* #char-container {
  display: none;
  flex-direction: row;
  position: relative;
} */

#char-container-2 {
  display: flex;
  flex-direction: row;
  position: relative;
}

#character-img-div {
  height: 90px;
  background-image: url('../src/assets/bg-1.png');

  background-size: cover;
  width: 90px;
  position: relative;
  bottom: 10px;
  border: 7px ridge var(--silver);
  box-shadow: 1px 1px 10px black;
}

#character-avatar-div {
  display: flex;
  justify-content: space-around;
  flex-direction: row;
  position: relative;
  top: 10px;
}

#character-card {
  height: auto;
  background-image: url('../src/assets/parchment-background.PNG');
  background-size: cover;
  position: relative;
  background-image: brightness(50%);
}

#equipment-card {
  height: auto;
  background-image: url('../src/assets/leather-background.PNG');
  background-size: cover;
}

#gold-line {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-left: 0px;
}

#gold-img {
  width: 50px;
  height: 50px;
}

.btn {
  color: black;
  margin: 15px;
  width: 200px;
  height: 75px;
  font-family: 'Yatra One', cursive;
  font-size: 20px;
  border: 8px ridge var(--gold);
  border-radius: 5px;
  background-color: var(--silver);
  box-shadow: 2px 2px 10px black;
}

a{
  appearance: button;
  text-decoration: none;
  width: 200px;
  text-align: center;
  flex-direction: column;
  justify-content: center;
}


#character-selection-div {
  display: flex;
  flex-direction: column;
  align-items: center;
  flex-wrap: wrap;
}

#play-btn {
  background-color: rgb(156, 19, 19);
}

#play-btn:hover {
  background-color: rgb(128, 15, 15);
}

#to-town-btn{
  background-color: var(--green);
  display: none;
}

#play-again {
  display: none;
}

#combat-div {

  align-items: center;
  padding-top: 0px;
  height: 700px;
  border-top: none;
}



button:hover {
  cursor: pointer;
  background-color: var(--grey)
}

a:hover{
  cursor: pointer;
  filter: brightness(75%);
}

.arena-placement {
  display: flex;
  flex-direction: row;
  width: 100px;
  height: 100px;
}

#arena {
  position: relative;
  background-image: url('../src/assets/bg-1.png');
  background-size: cover;
  margin: 10px;
  padding: 0;
  height: 350px;
  width: 400px;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  border: 10px ridge var(--silver);
  border-radius: 8px;
  box-shadow: 1px 1px 10px black;
}

.background-img {
  position: absolute;
  background-image: url('../src/assets/bg-1.png');
  background-size: cover;
  width:100%;
  height: 100%;
  filter: blur(2px);
}

#arena-banner {
  flex-direction: row;
  align-items: center;
  border: none;
  border: 10px ridge var(--gold);
  justify-content: space-around;
  margin-top: 0px;
  background-color: transparent;
  padding: 0px;
  margin:-10px;
  margin-bottom: 20px;
}

#turn-display {
  display: flex;
  align-items: center;
  padding-left: 20px;
  padding-right: 20px;
  border: 10px ridge var(--gold);
  border-radius: 10px;
  height: 50px;
  box-shadow: 1px 1px 10px black;
}

.stam-div {
  display: none;
}


@keyframes breath {
  from {
    transform: scale (1, 1);
   transform-origin: center bottom;
  }

  50% {
    transform: scale(1, 1.03);
    /* transform: translateY(0); */
  }

  to {
    transform:scale (1, 1);
    /* transform: translateY(0); */
  }
}

@keyframes heroAttackAni {
  from{
    transform: translateX(-60px);
  }

10%{
  transform: translateX(0px);
}

50%{
  transform: translateX(0px);
}

  to{
    transform: translateX(-100px);
  }
}
.static-display{
  animation: breath 1s linear infinite normal none;
  width: 150px;
  height: 150px;
  position: absolute;
  bottom: 35px;
  
}

.attack-display{
  animation: heroAttackAni 1s linear 1 normal none;
  width: 150px;
  height: 150px;
  position: relative;
  left: 120px;
  top: 25px;
}


#arena-monster-attack {
  display: none;
  position: relative;
  right: 50px;
  top: 25px;
}

.dmg-img {
  display: none;
  position: absolute;
}

.arena-placement {

  justify-content: center;
}

.health-bar {
  display: flex;
  flex-direction: column;
  position: absolute;
  top: 20px;
  align-content: center;
  margin-left: 5px;
  height: 10px;
  width: 120px;
  border: 3px solid black;
  align-items: left;
}

.stam-bar {
  display: flex;
  flex-direction: column;
  position: absolute;
  top: 40px;
  align-content: center;
  margin-left: 5px;
  height: 10px;
  width: 120px;
  border: 3px solid black;
  align-items: left;
}

.health-juice {
  background-color: red;
  width: 100%;
  height: 15px;
}

/* .stam-juice {
  background-color: green;
  width: 100%;
  height: 15px;
} */

#potion-div {
  position: absolute;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  width: 20px;
  height: 20px;
  top: 20px;
  left: 15px;
}

#potion-lid {
  position: relative;
  top: 5%;
  width: 20%;
  height: 20%;
  border: 2px solid black;
  border-bottom: 0px;
}

#potion-bottle {
  display: flex;
  flex-direction: row;
  width: 100%;
  height: 100%;
  border: 2px solid black;
  border-radius: 100px;
  align-items: end;
  justify-content: center;
}

#potion-juice {
  position: relative;
  background-color: rgb(241, 153, 168);
  width: 100%;
  height: 100%;
  border-radius: 100px;
}

#combat-UI-div {
  position: relative;
  width: 100%;
  display: flex;
  flex-direction: column;
  height: 220px;
  padding: 0;
  border-bottom: none;
  box-shadow: none;
}

#combat-log-parent-div {
  position: relative;
  top: 0px;
  height: 60px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

#combat-log {
  line-height: 20px;
  text-align: center;
  margin: 0;
}

#combat-btn-div {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  height: auto;

}

.combat-btn {
  margin: 10px;
  width: 120px;
}


footer {
  position: relative;
  bottom: 0px;
  display: flex;
  flex-direction: row;
  height: 100px;
  color: white;
  justify-content: center;
  align-items: center;
}

@media screen and (max-width: 1500px) {

  #char-container {
      flex-wrap: nowrap;
      flex-direction: column;
      align-items: center;
  }

  #character-card {
      width: 90%;

  }

  #equipment-card {
      width: 90%;
  }

  #combat-div {
      height: auto;
  }

  #combat-UI-div {
      height: 500px;
  }

  #combat-log-parent-div {
      height: 100px;
  }


  .combat-btn {
      width: 300px;
      margin: 15px;
  }

}

@media screen and (max-width: 1275px) {

  .menu-btn-div {
      display: flex;
      flex-direction: column;
  }
  #arena {
      width: 275px;
      height: 300px;
  }

  .health-bar {
      height: 10px;
      width: 90px;
      top: 10px;
  }

  .stam-bar {
      height: 10px;
      width: 90px;
      top: 30px;
  }

  #potion-div {
      height: 15px;
      width: 15px;
      top: 10px;
      left: 5px;
  }

  #potion-lid {
      border: 1px solid black;
      border-bottom: 0px;
  }

  #potion-bottle {
      border: 1px solid black
  }
}

@media screen and (max-width: 900px) {
  #char-select-header{
      font-size: 40px;
  }

  #character-img-div {
      width:75px;
      height: 75px;
      margin-top: 5px;
  }

  .btn {
      width: 150px;
  }

  .card{
      padding: 0px;
  }
}


@media screen and (max-width: 500px) {
  header {
      align-content: center;

  }

  h1 {
      font-size: 35px;
      text-align: center;
  }

  h2 {
      font-size: 30px;
  }

  h3 {
      font-size: 30px;
      margin: 0;
  }

  .container {
      width: 90%;
  }

  .card {
      height: auto;
  }

  #char-select-header{
      font-size: 40px;
  }

  p {
      font-size: 12px;
      margin: 5px;
      margin-left: 30px;
      line-height: 150%;
  }

  main {
      flex-wrap: wrap;
  }

  #char-container {
      flex-wrap: nowrap;
      flex-direction: column;
      align-items: center;
      justify-content: center;
  }

  #char-container-2 {
      flex-wrap: nowrap;
      flex-direction: column;
      align-items: center;
      justify-content: center;
  }

  #character-card{
      width:80%;
  }

  #equipment-card{
      width:80%;
  }

  #character-img-div {
      height: 90px;
      width: 90px;
      border: 5px ridge var(--silver)
  }

  #char-name {
      position: relative;
      font-size: 45px;
      top: 0px;
  }

  .card-header {
      font-size: 25px;
      margin-left: 15px;
      width: fit-content;
  }

  .card-item {
      font-size: 18px;
  }


  #combat-div {
      align-items: center;
      align-content: center;
      justify-content: center;
      height: auto;
      border-top: none;
  }


  #arena {
      height: 125px;
      width: 225px;
  }

  img {
      height: 60px;
      width: 60px;
  }

  .arena-img {
      width: 70px;
      height: 70px;
      position: absolute;
      top: 40px;
  }

  #arena-hero-attack {
      position: relative;
      left: 50px;
      top: 25px;

  }

  .static-display{
    width: 70px;
    height: 70px;
    position: absolute;
    bottom: 15px;
  }
  
  .attack-display{
    display:block;
    width: 70px;
    height: 70px;
    position: relative;
    top: 25px;
  }

  #arena-monster-attack {
      display: none;
      position: relative;
      right: 50px;
      top: 25px;
  }

  #arena-banner {
      background-image: none;
      flex-direction: row;
      border: 10px ridge var(--gold);
      justify-content: space-around;

      width: 100%;
      height: 100px;
  }

  #turn-display {
      padding: 10px;
      border: 10px double black;
      border-radius: 20px;
      text-align: center;
  }

  .health-bar {
      height: 5px;
      width: 70px;
      top: 5px;
  }

  .stam-bar {
      height: 5px;
      width: 70px;
      top: 13px;
  }

  #potion-div {
      height: 10px;
      width: 10px;
      top: 10px;
      left: 5px;
  }

  #potion-lid {
      border: 1px solid black;
      border-bottom: 0px;
  }

  #potion-bottle {
      border: 1px solid black
  }

  .stam-div {
      font-size: 8px;
  }

  #combat-UI-div {
      height: 250px;
  }

  #combat-btn-div {

      height: 200px;
  }

  .combat-btn {
      margin: 2px;
      width: 120px;
  }
}
